import { extend } from 'lodash';
export class TreeOptions {
  childrenField = 'children';
  displayField = 'name';
  fileTypeField = 'type'; // projectfolderfile
  pathField = 'path'; // 
  treeNodeTemplate: any = '{{ node.displayField }}'; // 
  hasCustomContextMenu = false; // 
  constructor(options = {}) { // option
    extend(this, options);
  }
}



