import { TreeNodeData } from '../models/tree-node-data';
import { METADATA_TYPE } from '../constants/metadata';
import { remove } from 'lodash';
import { NODE_REORGANIZER_RULE } from './tree.constant';
/**
 * 
 */
export class TreeDataReorganizer {

  public reorganizeTreeData(sourceTreeData: TreeNodeData) {
    if (null === sourceTreeData) {
      return;
    }

    const children: any[] = sourceTreeData.children;
    let alreadyCopyNodes: any[] = [];
    children.forEach(node => {
      if (node.type !== METADATA_TYPE.FILE) {
        return;
      }

      if (!this.isParentNode(node)) {
        return;
      }

      const childrenNodes: any[] = this.searchSameNameNodes(node, children);
      if (childrenNodes == null || childrenNodes.length <= 0) {
        return;
      }
      node.children = childrenNodes;

      alreadyCopyNodes = alreadyCopyNodes.concat(childrenNodes);
    });

    // 
    if (alreadyCopyNodes.length <= 0) {
      return;
    }
    remove(children, function (child) {
      return alreadyCopyNodes.includes(child);
    });
  }

  /**
   * 
   * @param node 
   */
  private isParentNode(node: TreeNodeData): boolean {

    let isParent = false;
    const nodeFullName = node.name;
    if (nodeFullName.split('.').length === 2) { // '.'2
      // parent node
      for (var rule of NODE_REORGANIZER_RULE.AttachRule) { // forEach
        if (nodeFullName.includes(rule.parentNode.suffix)) {
          isParent = true;
          break;
        }
      }
    }
    return isParent;
  }

  /**
   * parent
   * @param nodeFullName 
   */
  private getParentName(nodeFullName: string): string {
    if (this.isUndefinedOrNullOrEmpty(nodeFullName)) {
      return null;
    }

    return nodeFullName.split('.')[0];
  }

  /**
   * child
   * @param nodeFullName 
   */
  private getChildName(nodeFullName: string, parentSuffix: string): string {
    if (this.isUndefinedOrNullOrEmpty(nodeFullName)) {
      return null;
    }

    if (this.isUndefinedOrNullOrEmpty(parentSuffix)) {
      return null;
    }

    // 
    let childName = null;
    for (var rule of NODE_REORGANIZER_RULE.AttachRule) {
      if (parentSuffix === rule.parentNode.suffix) {
        const splittedArray = nodeFullName.split(rule.childrenNode.splitter);
        if (splittedArray.length > 1) {
          childName = splittedArray[0];
          break;
        }
      }
    }

    return childName;
  }

  /**
   * 
   */
  searchSameNameNodes(node: TreeNodeData, nodeArray: TreeNodeData[]): TreeNodeData[] {
    const srcNodeName = this.getParentName(node.name);
    if (null === srcNodeName) {
      return null;
    }

    const srcNodeSuffix = this.getNodeSuffix(node.name);
    if (null === srcNodeSuffix) {
      return null;
    }

    const sameNameNodes: TreeNodeData[] = [];
    nodeArray.forEach(localNode => {
      const _nodeName = this.getChildName(localNode.name, srcNodeSuffix);
      if (!(localNode === node)) { // 
        // 
        for (var rule of NODE_REORGANIZER_RULE.AttachRule) { // forEach
          if (srcNodeName === _nodeName && srcNodeSuffix === rule.parentNode.suffix) {
            sameNameNodes.push(localNode);
            break;
          }
        }
      }
    });

    return sameNameNodes;
  }

  /**
   * 
   * @param fileFullName 
   */
  private getNodeSuffix(nodeFullName: string): string {
    if (this.isUndefinedOrNullOrEmpty(nodeFullName)) {
      return null;
    }
    const length = nodeFullName.split('.').length;
    return nodeFullName.split('.')[length - 1];
  }

  /**
   *  undefined  null  empty
   * @param str 
   */
  isUndefinedOrNullOrEmpty(str: string): boolean {
    if (undefined === str || null === str || str.length <= 0) {
      return true;
    }

    return false;
  }
}



