/**
 * 
 */
export const NODE_REORGANIZER_RULE = {

    // 
    AttachRule: [{
        // :
        // a  b
        // a.x  b.x //
        // a.b.x  a.c.x
        // ...
        
        // parent node:  + 
        // '.'2'hlp'0
        parentNode: {
            suffix: 'hlp'
            // // 
            // splitter: '.',
            // lengthAfterSplit: 2,
            // nameIndexAfterSplit: 0
        },
        // parentchildren node
        // '.''_'0
        childrenNode: {
            splitter: '_'
            // // 
            // nameIndexAfterSplit: 0
        }},{
            parentNode: {
                suffix: 'hlp'
            },
            childrenNode: {
                splitter: '.'
            }
        },{
            parentNode: {
                suffix: 'cs'
            },
            childrenNode: {
                splitter: '.'
            }
        }
    ]
};


